import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import { common } from "@/assets/js/request.js";
Vue.use(Vuex)

const state = {
    hotelId: '',
    HotelInfo: '',
    roomInTime: '',
    roomOutTime: '',
    roomInfo: '',
}
const mutations = {
    // 缓存酒店id
    storeHotelid(state, Info) {
        state.hotelId = Info;
        sessionStorage.setItem("hotelId", Info);
    },
    // 缓存酒店信息
    storeHotel(state, Info) {
        state.HotelInfo = Info
    },
    // 缓存房间信息
    storeRoom(state, Info) {
        state.roomInfo = Info
    },
    // 缓存时间信息
    storeInTime(state, Info) {
        state.roomInTime = Info
    },
    storeOutTime(state, Info) {
        state.roomOutTime = Info
    },
}

const actions = {
    getHotelInfo({ commit, state }) {
        return new Promise((resolve, reject) => {
            common.hotelInfo({
                Suffix: sessionStorage.getItem("hotelId"),
                fn: data => {
                    commit("storeHotelid", data.hotelId);
                    commit("storeHotel", data);
                    resolve()
                }
            });

        })
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions
})



