//将分钟变成小时
export function countDown(val) {
    var newtime = null;
    setInterval(function () {
        var utc = (val - new Date().getTime()) / 1000;
        var tian = Math.floor(utc / (24 * 60 * 60));
        var shi = Math.floor((utc / 60 / 60) % 24);
        var fen = Math.floor((utc / 60) % 60);
        var miao = Math.floor(utc % 60);
        if (tian < 10) {
            tian = "0" + tian;
        }
        if (shi < 10) {
            shi = "0" + shi;
        }
        if (fen < 10) {
            fen = "0" + fen;
        }
        if (miao < 10) {
            miao = "0" + miao;
        }
        return newtime = tian + "天" + shi + "时" + fen + "分" + miao + "秒";
    }, 1000);
    return newtime
}

