//判断是数字
export function integertest(rule, value, callback) {
    if (!/^\d+$/.test(value)) {
        callback(new Error("请输入正确的配送时间"));
    } else {
        callback();
    }
};
//判断是0-1的小数
export function checknum(rule, value, callback) {
    if (!/^(0.\d+|0|1)$/.test(value)) {
        callback(new Error("请输入正确的折扣率"));
    } else {
        callback();
    }
};

//将0 1 转为 true false
export function option(val) {
    if (val == 0) {
        return false
    } else {
        return true
    }
}
//将时间转为 2017.8.16
export function TimeFormat(val) {
    if (val) {
        var mm = new Date(val).getMinutes();
        if (mm < 10) {
            mm = "0" + mm
        }
        return new Date(val).getHours() + ':' + mm
    } else {
        return null
    }

}


