import Vue from 'vue'
import Router from 'vue-router'
const home = r => require.ensure([], () => r(require('@/components/home')), 'home')
const Book = r => require.ensure([], () => r(require('@/components/Book')), 'Book')
const Activitydetails = r => require.ensure([], () => r(require('@/components/Activitydetails')), 'Activitydetails')
const BookImmediately = r => require.ensure([], () => r(require('@/components/BookImmediately')), 'BookImmediately')
const Payment = r => require.ensure([], () => r(require('@/components/Payment')), 'Payment')
const MealList = r => require.ensure([], () => r(require('@/components/MealList')), 'MealList')
const Defrayal = r => require.ensure([], () => r(require('@/components/Defrayal')), 'Defrayal')
const Quit = r => require.ensure([], () => r(require('@/components/Quit')), 'Quit')
const Me = r => require.ensure([], () => r(require('@/components/Me')), 'Me')
const Myroom = r => require.ensure([], () => r(require('@/components/Myroom')), 'Myroom')
const Mymeal = r => require.ensure([], () => r(require('@/components/Mymeal')), 'Mymeal')
const Mydeposit = r => require.ensure([], () => r(require('@/components/Mydeposit')), 'Mydeposit')
const Mycard = r => require.ensure([], () => r(require('@/components/Mycard')), 'Mycard')
Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      component: home,
      redirect: '/book'
    },
    {
      path: '/book',
      component: home,
      redirect: '/book/roomSet',
      children: [
        { path: '/book/roomSet', component: Book, name: 'Book', meta: { title: '多粉酒店' } },
        { path: '/book/roomSet/:hotelId', component: Book, name: 'Book1', meta: { title: '多粉酒店' } },
        { path: '/book/activitydetails/:activityType/:activityId', component: Activitydetails, name: 'Activitydetails', meta: { title: '单人房' } },
      ]
    },
    {
      path: '/meal',
      component: home,
      redirect: '/meal/list',
      children: [
        { path: '/meal/list', component: MealList, name: 'MealList', meta: { title: '客房订餐' } },
      ]
    },
    {
      path: '/quit',
      component: home,
      redirect: '/quit/room',
      children: [
        { path: '/quit/room', component: Quit, name: 'Quit', meta: { title: '预约退房' } }
      ]
    },
    {
      path: '/administrators',
      component: home,
      redirect: '/administrators/my',
      children: [
        { path: '/administrators/my', component: Me, name: 'Me', meta: { title: '我的' } },
        { path: '/administrators/myroom', component: Myroom, name: 'Myroom', meta: { title: '房间订单' } },
        { path: '/administrators/mymeal', component: Mymeal, name: 'Mymeal', meta: { title: '我的订餐' } },
        { path: '/administrators/mydeposit', component: Mydeposit, name: 'Mydeposit', meta: { title: '我的押金' } },
        { path: '/administrators/mycard', component: Mycard, name: 'Mycard', meta: { title: '我的房卡' } }
      ]
    },
    {
      path: '/bookImmediately/:categoryId',
      component: BookImmediately,
      name: 'bookImmediately',
      meta: { title: '立即预定' }
    },
    {
      path: '/payment/:orderId',
      component: Payment,
      name: 'payment',
      meta: { title: '支付' }
    },
    {
      path: '/defrayal/:orderId',
      component: Defrayal,
      name: 'Defrayal',
      meta: { title: '支付' }
    },
    {
      path: '*',
      component: home,
      redirect: '/book'
    }
  ]
})



