import $http from "./http.js"
//公共模块
const common = {
    getconfig(obj) {
        return $http.get({
            url: "/mobile/78CDF1/common/config",
            params: obj.params,
            fn: obj.fn
        })
    },
    // 查询
    hotelInfo(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //取消订单
    cancelOrder(obj) {
        return $http.post({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //客房订单详情
    paymentOrder(obj) {
        return $http.get({
            url: "/mobile/78CDF1/room/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    orderpay(obj) {
        return $http.get({
            url: "/mobile/78CDF1/room/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 查询 发票列表
    invoice(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //vip信息
    vipinfo(obj) {
        return $http.get({
            url: "/mobile/78CDF1/common/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}
// 首页模块 homePage
const homePage = {
    // 查询 全部活动列表
    activitylist(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //首页房型列表
    roomCategory(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 查询 具体活动列表
    specificActivity(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    }
}
//  订房模块
const roomBooking = {
    // 查询 全部活动列表
    getRoomInfo(obj) {
        return $http.get({
            url: "/mobile/78CDF1/room/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //获取价格
    getPrice(obj) {
        return $http.post({
            url: "/mobile/78CDF1/room/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    Submitobj(obj) {
        return $http.post({
            url: "/mobile/78CDF1/room/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}
//退房模块
const QuitModular = {
    // 查询 已入住订单
    getQuitrooms(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //退房
    Quitrooms(obj) {
        return $http.post({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}
//订餐模块
const foodBooking = {
    // 查询 菜单列表
    foodlist(obj) {
        return $http.get({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 保存订餐的订单
    foodsettlement(obj) {
        return $http.post({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //查询订餐的订单详情
    foodOrderDetails(obj) {
        return $http.get({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 保存订餐的订单
    foodpay(obj) {
        return $http.post({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    }
}
//我的模块
const Personal = {
    // 查询 个人信息
    myInfo(obj) {
        return $http.get({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 查询 我的押金
    getdeposit(obj) {
        return $http.get({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 删除 我的押金
    deletedeposit(obj) {
        return $http.post({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //查询订餐订单
    foodOrder(obj) {
        return $http.get({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //查询订餐订单
    roomsOrder(obj) {
        return $http.get({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //我的商城、会员卡、个人中心 链接
    linkList(obj) {
        return $http.get({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 查询 我的房卡
    roomCard(obj) {
        return $http.get({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 查询 我的房卡
    getcode(obj) {
        return $http.get({
            url: "/mobile/78CDF1/my/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },

}
export {
    common,
    homePage,
    QuitModular,
    roomBooking,
    foodBooking,
    Personal
}


