// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './assets/js/store'
require('swiper/dist/css/swiper.css')
import './assets/css/swiper.min.css'
import './assets/js/swiper.min.js'
import './assets/fonts/iconfont'
import './assets/css/main.less'
import * as filters from './assets/js/filters'
import { Toast } from "mint-ui";
Vue.prototype.Toast = Toast
Object.keys(filters).forEach(key => {
  Vue.filter(key, filters[key])
})
// var windowWidth = $(window).width()
// $('html').css('font-size', windowWidth/320*100+"%")
// document.getElementsByTagName('html')[0].style.fontSize = window.innerWidth / 7.5 + 'px';
document.getElementsByTagName('html')[0].style.fontSize = document.getElementsByTagName("html")[0].offsetWidth / 7.5 + 'px';
Vue.config.productionTip = false
router.beforeEach((to, from, next) => {
  if (to.meta) {
    if (to.path.indexOf('/book/roomSet') != -1) {

    } else {
      document.title = to.meta.title
    }


  }
  next();
})
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
})



